.Dossier {
    width: 250px;
    margin: 20px;
    color: white;
    overflow: hidden;   

    .couverture {
        img {
            width: 100%;
        }
        
        .deplacer {
            vertical-align: middle;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            background-color: rgba(39, 39, 39, 0.3);
        }
    }

    .info {
        font-size: 0.75em;
        line-height: 1.2em;
        padding: 15px 16px;
    }

    .BtnPlus {
        border: none;
        padding: 0;
    }
}