.Entete {
    position: sticky;
    width: 100%;
    margin: 0;
    padding: 5px;
    color: #fff;
    background-image: linear-gradient(to right, #000000 , #383838);
    display: flex;
    align-items: center;

    .logo {
        margin-left: 10px;
    }

    .avatar {
        display: flex;
        margin-left: auto;
        margin-right: 20px;
        margin-bottom: 0;
        align-items: center;

        img {
            width: 100%;
        }
    }
}